import {queryTrans}  from '@/common/api.js'
import searchInput from '@/components/search-input.vue'

export default {
	components:{
		searchInput
	},
	filters:{
		fixedTo2(value){
			if(value){
				var newVal=parseFloat(value);
				return newVal.toFixed(2);
			}else{
				return '0.00';
			}
		}
	},
	data() {
		return {
			searchItems:{
				placeholder:'请输入流水号',
				maxlength:30,
			},
			headers:['流水号','交易门店','交易时间','实付金额'],
			currentPage:1,
			grpCode:'',
			trans:[],
			hasLoad:false,
			loading:true,
			
		};
	},
	onPullDownRefresh() {
		this.currentPage=1;
		this.queryTrans(true);
	},
	onLoad(options) {
		this.grpCode=options.grpId;
		this.queryTrans();
	},
	onReachBottom() {
		this.queryTrans();
	},
	methods:{
		queryTrans(hideLoading){
			if(this.hasLoad){
				uni.hideNavigationBarLoading();
				uni.stopPullDownRefresh();
				return;
			}
			
			uni.showNavigationBarLoading();
			this.loading=true;
			queryTrans(this.grpCode,this.currentPage,hideLoading).then(res=>{
				this.loading=false;
				this.currentPage++;
				uni.hideNavigationBarLoading();
				uni.stopPullDownRefresh();
				this.trans=this.trans.concat(res.data);
				if(this.trans.length>=res.total)
					this.hasLoad=true;
			}).catch(e=>{
				this.loading=false;
				this.$refs.notify.show("请求失败!",this.COLORCONFIG.danger)
			})
		},
		confirm(val){
			
		}
	}
}
